/*font sizes*/
body {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 50px 1fr 50px;
  grid-template-areas: "header" "main" "footer";
  height: 100vh;
  width: 100%; }
  body header {
    grid-area: header;
    background-color: darkgray;
    display: flex; }
    body header .navbtn {
      font-size: 25px;
      margin: auto 0 auto 5%; }
    body header h1 {
      font-size: larger;
      margin-left: auto;
      margin-right: 10px; }
  body aside, body #navmenu {
    grid-area: side;
    background-color: #4865dc;
    color: #eaeaea;
    width: 100%;
    display: none;
    flex-direction: column; }
    body aside .fa-home, body #navmenu .fa-home {
      font-size: xx-large;
      color: #eaeaea;
      margin: 40px auto; }
    body aside a, body #navmenu a {
      margin: 20px 0;
      text-decoration: none; }
      body aside a:hover .menubtn, body #navmenu a:hover .menubtn {
        background-color: #3353d8;
        font-weight: bolder;
        font-size: x-large; }
    body aside .menubtn, body #navmenu .menubtn {
      width: 100%;
      height: 50px;
      background-color: #2747ca;
      color: #eaeaea;
      display: flex;
      justify-content: center;
      align-items: center; }
      body aside .menubtn span, body #navmenu .menubtn span {
        font-size: larger;
        text-decoration: none;
        color: #eaeaea;
        font-weight: bold; }
  body #navmenu {
    display: none;
    position: absolute;
    top: 50px;
    left: 0; }
  body main {
    padding: 0 10px;
    font-size: 25px;
    overflow-y: scroll; }
    body main section {
      height: 1000px; }
      body main section .statistics .pageviews {
        margin-top: 50px; }
        body main section .statistics .pageviews .totalviews {
          width: 200px;
          margin: 0 auto;
          background-color: #9eadec;
          border: solid #4865dc 1px;
          border-radius: 30px;
          display: flex;
          justify-content: center;
          align-content: center;
          flex-wrap: wrap; }
          body main section .statistics .pageviews .totalviews span:nth-of-type(1) {
            font-size: 20px;
            position: relative;
            top: -10px;
            background-color: #4865dc;
            color: #eaeaea;
            padding: 10px;
            border-radius: 10px; }
          body main section .statistics .pageviews .totalviews span:nth-of-type(2) {
            font-size: 25px;
            margin-bottom: 20px;
            padding: 5px; }
        body main section .statistics .pageviews .pageviewcontaienr {
          display: flex;
          justify-content: space-around;
          flex-wrap: wrap;
          margin: 10px auto; }
        body main section .statistics .pageviews span {
          font-size: 20px; }
        body main section .statistics .pageviews .viewsfrom {
          width: 90%;
          margin: 20px auto;
          background-color: #e4e4e4; }
          body main section .statistics .pageviews .viewsfrom div {
            padding: 10px 10px;
            display: grid;
            grid-template-columns: 60% 20% 20%;
            grid-template-areas: "url month lifetime";
            justify-content: center; }
            body main section .statistics .pageviews .viewsfrom div:nth-child(1) {
              grid-area: url; }
            body main section .statistics .pageviews .viewsfrom div:nth-child(2) {
              grid-area: month; }
            body main section .statistics .pageviews .viewsfrom div:nth-child(3) {
              grid-area: lifetime; }
            body main section .statistics .pageviews .viewsfrom div:nth-of-type(odd) {
              background-color: #b1b1b1; }
        body main section .statistics .pageviews .progressionovertime {
          width: 50%; }
          body main section .statistics .pageviews .progressionovertime .bar1total {
            width: 100%;
            height: 30px;
            background-color: #646464; }
            body main section .statistics .pageviews .progressionovertime .bar1total .bar1amount {
              width: 60%;
              height: 28px;
              position: relative;
              left: 1px;
              top: 1px;
              background-color: #0ac80a; }
      body main section .users form {
        padding: 10px;
        margin: 0;
        height: 200px;
        overflow-y: scroll; }
        body main section .users form div {
          width: 100%;
          height: 20px;
          display: flex;
          justify-content: space-between; }
          body main section .users form div span {
            padding: 0 10px;
            font-size: 20px; }
      body main section .users #popupinfoB1btn {
        cursor: pointer; }
      body main section .users #popupinfoB1 {
        width: 90%;
        border: black 2px solid;
        background-color: #c8d1f5;
        display: flex;
        display: none;
        flex-direction: column;
        position: relative;
        z-index: 2;
        top: -50px;
        left: 50%;
        transform: translate(-50% 0); }
        body main section .users #popupinfoB1 div:nth-child(1) {
          display: flex;
          flex-direction: row;
          align-items: center; }
          body main section .users #popupinfoB1 div:nth-child(1) h4 {
            margin: 20px 40px; }
          body main section .users #popupinfoB1 div:nth-child(1) span {
            margin-left: 40px; }
  body .webshop div {
    display: flex;
    flex-direction: column; }
    body .webshop div div {
      flex-direction: row;
      justify-content: space-between;
      padding: 0 10%; }
  body .webshop .refundreq {
    cursor: pointer; }
  body .webshop .refundreqDropdown {
    display: none;
    background-color: lightgrey;
    margin: 0 10%;
    flex-direction: column; }
  body .highlightinfo {
    background-color: lightgray;
    height: 20px;
    border: darkgrey 1px solid; }
  body footer {
    grid-area: footer;
    background-color: gray; }

@media screen and (min-width: 768px) {
  body {
    grid-template-columns: 15% 85%;
    grid-template-rows: 50px 1fr 50px;
    grid-template-areas: "side header" "side main" "side footer";
    height: 100vh; }
    body aside {
      display: flex; }
    body main {
      padding: 0 10%; }
  header .navbtn {
    display: none; }
  .webshop .refundreqDropdown {
    margin: 0 25%; } }
